@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap);
nav{
    background-color: rgb(230, 57, 70);
}
.navbar{
    padding: 10px 20px;
}
.navbar-light .navbar-brand{
    color: #fff;
    font-family: 'Rock Salt', cursive;
}
.navbar-expand-lg .navbar-nav .nav-link{
    color: #fff;
    padding: 0px;
    margin-left: 20px;
    font-weight: bold;
}
.navbar-light .navbar-toggler{
    background-color: #fff;
}
.navbar-light .navbar-brand:focus, .navbar-light .navbar-brand:hover{
    color: #fff;
}
.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover{
    color: rgba(255, 255, 255, 0.7);
}
.navbar-light .navbar-nav .nav-link:active{
    color: rgba(255, 255, 255, 0.7);
}

/* media queries */
@media(max-width:800px){
    .navbar-expand-lg .navbar-nav .nav-link{
        margin-left: 0px;
    }
    .navbar-nav{
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 10px;
        margin-top: 10px;
    }
    .navbar-nav li{
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        margin: auto;
        margin-top: 5px;
        margin-bottom: 5px;
    }
}
.products {
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
}

.products .product-detail {
    width: 50%;
    padding-left: 20px;
}

.products .product-detail p {
    padding: 20px 0px;
    color: #525252;
}

.products .product-detail h1 {
    color: #004470;
    font-size: 40px;
    text-shadow: 0px 0px 25px rgba(68, 122, 156, 0.30);
}

.products .product-image img {
    width: 300px;
    border-radius: 50%;
    height: 300px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.30);
}

.product-image {
    padding-right: 20px;
    width: 50%;
    text-align: right;
}

.order-btn {
    color: #E63946;
    font-weight: bold;
    cursor: pointer;
    padding: 10px 20px;
    border: 2px solid #E63946;
    border-radius: 7px;
    background-color: #F1FAEE;
    transition: 0.3s;
}

.order-btn:hover {
    color: white;
    background-color: #E63946;
}

/* media queries */
@media (max-width:450px){
    .products{
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
    }
    .product-image{
        width: auto;
        padding-right: 0px;
    }
    .products .product-image img{
        width: 250px;
        height: 250px;
    }
    .products .product-detail{
        width: auto;
        text-align: center;
        padding: 20px;
        margin-top: 20px;
    }
    .products .product-detail p{
        margin-bottom: 0px;
        /* padding: 5px 0px; */
    }
}
@media(max-width:375px){
    .products{
        padding-top: 20px;
    }
    .products .product-image img{
        width: 200px;
        height: 200px;
    }
    .products .product-detail{
        margin-top: 0px;
    }
    .products .product-detail p{
        padding: 5px 0px;
    }
}
.products-almond{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 50px;
}
#almond{
    position: relative;
}
.almonds-background {
    margin-top: 25px;
    -webkit-clip-path: polygon(0% 30%, 100% 0%, 100% 70%, 0% 100%);
            clip-path: polygon(0% 30%, 100% 0%, 100% 70%, 0% 100%);
    background-color: #A8DADC;
    width: 100%;
    height: 250px;
    position: absolute;
    z-index: -1;
}

.products-almond .almond-detail {
    width: 50%;
    padding: 20px;
    text-align: right;
}

.products-almond .almond-detail p {
    padding: 20px 0px;
    color: #525252;
}

.products-almond .almond-detail h1 {
    color: #004470;
    font-size: 40px;
    text-shadow: 0px 0px 25px rgba(68, 122, 156, 0.30);
}

.products-almond .almond-image img {
    width: 300px;
    border-radius: 50%;
    height: 300px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.30);
}

.almond-image {
    padding-left: 20px;
    width: 50%;
    text-align: left;
}

/* media queries */
@media(max-width:450px){
    .products-almond{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 40px;
    }
    .almond-image{
        width: auto;
        padding: 0px;
    }
    .products-almond .almond-detail{
        width: auto;
        text-align: center;
        margin-top: 20px;
    }
    .products-almond .almond-detail p{
        margin-bottom: 0px;
    }
    .almonds-background{
        margin-top: 250px;
    }
    .products-almond .almond-image img {
        width: 250px;
        height: 250px;
    }
}
@media(max-width:375px){
    .products-almond .almond-detail p{
        padding: 5px 0px;
    }
    .products-almond .almond-image img {
        width: 200px;
        height: 200px;
    }
}
#chips{
    position: relative;
    /* margin-top: 50px; */
}
.chips-background {
    margin-top: 20px;
    -webkit-clip-path: polygon(0% 30%, 100% 0%, 100% 70%, 0% 100%);
            clip-path: polygon(0% 30%, 100% 0%, 100% 70%, 0% 100%);
    background-color: rgba(230, 57, 70, 0.5);
    width: 100%;
    height: 250px;
    position: absolute;
    z-index: -1;
}

/* media queries */
@media(max-width:450px){
    .chips-background{
        margin-top: 250px;
    }
}
@media(max-width:375px){
    .chips-background{
        margin-top: 200px;
    }
}
.all-products {
    background-color: rgba(230, 57, 70, 0.7);
    margin-top: 100px;
    padding-bottom: 30px;
}

.all-products h1 {
    color: #FFFFFF;
    font-size: 35px;
    text-align: center;
    padding-top: 15px;
}

.all-products-images {
    display: flex;
    justify-content: space-evenly;
    padding-top: 20px;
}

.all-products-images img {
    height: 200px;
    width: 200px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, .5);
}

.popcorn-image,
.almonds-image,
.chips-image {
    text-align: center;
}

.popcorn-image p,
.almonds-image p,
.chips-image p {
    color: #F1FAEE;
    font-weight: 500;
    font-size: 20px;
    padding: 10px 0px;
    margin-bottom: 15px;
}

.popcorn-image a,
.almonds-image a,
.chips-image a {
    text-align: center;
    font-weight: bold;
    color: #F1FAEE;
    text-decoration: none;
    cursor: pointer;
    padding: 10px 20px;
    border: 2px solid #F1FAEE;
    border-radius: 7px;
    background-color: rgba(230, 57, 70, 0.05);
    transition: 0.3s;
}

.popcorn-image a:hover,
.almonds-image a:hover,
.chips-image a:hover {
    color: rgba(230, 57, 70, 0.7);
    background-color: #F1FAEE;
}

/* media queries */
@media(max-width:450px){
    .all-products-images{
        flex-direction: column;
    }
    .popcorn-image{
        margin-bottom: 40px;
    }
    .almonds-image{
        margin-bottom: 40px;
    }
    .chips-image{
        margin-bottom: 40px;
    }
    .all-products{
        margin-top: 50px;
    }
}
.about-us {
    background-image: linear-gradient(to right, rgba(69, 123, 157, 1),
        rgba(73, 170, 127, 1));
    margin-top: 25px;
    padding-bottom: 30px;
}

.about-us h1 {
    text-align: center;
    font-weight: 700;
    font-size: 35px;
    color: #1D3557;
    padding-top: 15px;
}

.about-us-div {
    display: flex;
    justify-content: space-between;
    padding-top: 25px;
}

.about-us-div .left-div {
    width: 50%;
    text-align: left;
    padding-left: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.about-us-div .left-div p {
    color: rgba(241, 250, 238, 0.9);
}

.social-icons img {
    width: 50px;
    height: 50px;
    -webkit-filter: invert(100%);
            filter: invert(100%);
    margin-right: 20px;
}

.about-us-div .right-div {
    width: 50%;
    text-align: right;
    padding-right: 40px;
}

.right-div img {
    width: 250px;
    height: 250px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, .5);
}

/* media queries */
@media(max-width:768px){
    .about-us-div{
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        width: auto;
    }
    .about-us-div .right-div{
        width: auto;
        text-align: center;
        padding: 0px;
    }
    .about-us-div .left-div {
        width: auto;
        text-align: center;
        padding: 0px 20px;
        margin-top: 20px;
    }
    .social-icons{
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }
    .social-icons img {
        margin-right: 0px;
    }
}
.contact-us{
    text-align: center;
    background-image: linear-gradient(to right,rgba(29, 53, 87, 0.75),
    rgba(230, 57, 70, 0.75));
    margin-top: 25px;
    padding-bottom: 30px;
}
.contact-us-div h1{
    text-align: center;
    padding-top: 15px;
    font-size: 35px;
    color: white;
}
.contact-us-div p{
    padding-bottom: 15px;
    font-size: 17px;
}
.logo{
    font-family: 'Rock Salt', cursive;
    font-weight: 100;
    color: white;
}
.contact-us-div form{
    display: flex;
    flex-direction: column;
    align-items: center;
}

form input,form .feedback{
    width: 400px;
    margin-bottom: 10px;
    padding: 10px 10px;
    border-radius: 7px;
    background: rgba(0, 0, 0, 0.3);
    border: none;
    outline: none;
    color: white;
}
form .feedback{
    resize: vertical;
}
::-webkit-input-placeholder{
    color: rgba(0, 0, 0, 0.7);
    font-weight: 600;
}
:-ms-input-placeholder{
    color: rgba(0, 0, 0, 0.7);
    font-weight: 600;
}
::placeholder{
    color: rgba(0, 0, 0, 0.7);
    font-weight: 600;
}
.form-btn{
    width: 150px;
    margin-top: 10px;
    color: #1D3557;
    font-weight: bold;
    cursor: pointer;
    padding: 10px 20px;
    border: 2px solid #457B9D;
    border-radius: 7px;
    background: rgba(69, 123, 157, 0.3);
    transition: .3s;
}
.form-btn:hover{
    color: white;
    background: #457B9D;
}
.horizontal-line{
    background-color: rgba(0, 0, 0, 0.1);
    width: 70%;
    height: 10px;
    border-radius: 50px;
    margin: 25px auto;
}
.address-div h1{
    color: #48C7A8;
    font-size: 30px;
}
.address-div p{
    font-weight: 500;
    color: white;
    line-height: 2;
    text-align: center;
}

/* media queries */
@media(max-width:450px){
    .contact-us-div form{
        width: 90%;
        margin:auto;
    }
    form input, form .feedback{
        width: 100%;
    }
    .form-btn{
        width: auto;
    }
}
.footer-section{
    margin-top: 25px;
    background-color: #E63946;
}
.footer-divider{
    background-color: rgba(0, 0, 0, 0.3);
    height: 1px;
    width: 30%;
    margin: auto;
}
#copyright{
    padding: 5px 0px;
    text-align: center;
    color: white;
    margin-bottom: 0px;
}
*{
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}
html{
  scroll-behavior: smooth;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(241, 250, 238)!important;
}
